<template>
  <div id="Login">
    <input placeholder="Your name .." type="text" v-model="name" @keyup.enter="formSubmit">
    <button @click="formSubmit">Start Chatting</button>
  </div> 
</template>

<script>

export default {
  data() {
    return {
      name:""
    };
  },

  methods: {
    formSubmit() {
          localStorage.setItem('name', this.name);  
          this.$store.commit('UPDATE_USER');
          this.$router.push("/chat");
          // window.location.href="/chat"
    },
  },
};
</script>

<style scoped>
#Login{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  background-color: #8b0000c2;
  width: 300px;
  height: 200px;
  vertical-align: center;
  border: 1px solid white;
  border-radius: 7px;
}
input{
    display: block;
    position: relative;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -40%);
    border-radius: 3px;
    border: 1px solid #8b0000c2;
    min-height: 40px;
    padding-left: 10px;

}
input:focus {
    outline: unset;
}
button{
    display: block;
    position: relative;
    left: 50%;
    top: 55%;
    width: 83%;
    transform: translate(-50%, -60%);
    background-color: #333;
    color: white;
    border: 1px solid #333;
    border-radius: 5px;
    padding:5px;
    transition: background-color 0.5s ease-out;

}
button:focus{
    outline: unset;
}
button:hover{
    background-color: white;
    color:black;
    border-color: white;
    transition: background-color 0.5s ease-in;
    
}
</style>