<template>
  <div
    class="container"
  >
      <hr />
    <div :class="[isSameUser ? 'outgoing_msg' : 'incoming_msg']">
      <div v-show="!isSameUser" :class="[isSameUser ? '' : 'incoming_msg_img', 'text-center'] ">
        <img
          src="https://ptetutorials.com/images/user-profile.png"
          alt="sunil"
        />
        <p class="image-name"> {{msg.sender}} </p>
      </div>
      <div :class="[isSameUser ? 'sent_msg' : 'received_msg']">
        <div :class="[isSameUser ? '' : 'received_withd_msg']">
          <p>
            {{msg.text}}
          </p>
          <span class="time_date">{{msgDate}}</span>
          <!-- <span class="time_date"> 11:01 AM | Today</span> -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["msg"],
  data() {
    return {
      isSameUser: false,

    };
  },
  methods: {},

  created() {
    // if(this.currentUsername === this.msg.sender)
    //   this.isSameUser = true;

    

    if(!this.$store.state.user){
      this.$router.push("/");
    }

    if(this.$store.state.user.user === this.msg.sender){
      this.isSameUser = true;
    }
  },

  computed: {
    msgDate(){

      // const ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(this.msg.sentAt.toDate());
      // const mo = new Intl.DateTimeFormat('en', { month: 'short' }).format(this.msg.sentAt.toDate());
      // const da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(this.msg.sentAt.toDate());

      // return da + " " + mo + " " + ye + " ";

      const date = this.msg.sentAt.toDate();

      var locale = window.navigator.userLanguage || window.navigator.language;
    
      return date.toLocaleDateString(locale, {
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  },
};
</script>

<style scoped>
.container{
    max-width:700px;
    margin:auto;
    /* height: 80%;     */
}
img{ max-width:100%;}

.image-name{
    font-size: 14px;
    color: black;
    width: 100%;
}

.incoming_msg_img {
  display: inline-block;
  width: 10%;
  word-wrap:break-word;
  height: 5px;
}
.received_msg {
  display: inline-block;
  padding: 0 0 0 10px;
  vertical-align: top;
  width: 80%;
 }
 .received_withd_msg p {
  background: #ebebeb none repeat scroll 0 0;
  border-radius: 3px;
  color: black;
  font-size: 14px;
  margin: 0;
  margin-left: 10px;
  padding: 5px 10px 5px 12px;
  width: 100%;
  min-height: 25px;
}
.time_date {
  color: #747474;
  display: block;
  font-size: 12px;
  margin: 8px 0 0;
  margin-left: 10px;
}
.received_withd_msg { width: 57%;}
.mesgs {
  float: left;
  padding: 30px 15px 0 25px;
  width: 60%;
}

 .sent_msg p {
  background: #8B0000 none repeat scroll 0 0;
  background-color: #8b0000c7;
  border-radius: 3px;
  font-size: 14px;
  margin: 0; color:#fff;
  padding: 5px 10px 5px 12px;
  width:100%;
  min-height: 25px;
}
.outgoing_msg{ overflow:hidden; margin:26px 0 26px;}
.sent_msg {
  float: right;
  width: 46%;
}
.input_msg_write input {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  color: #4c4c4c;
  font-size: 15px;
  min-height: 48px;
  width: 100%;
}

.type_msg {border-top: 1px solid #c4c4c4;position: relative;}
.msg_send_btn {
  background: #8b0000c2 none repeat scroll 0 0;
  border: medium none;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  font-size: 17px;
  height: 33px;
  position: absolute;
  right: 0;
  top: 11px;
  width: 33px;
}
.messaging { padding: 0 0 50px 0;}
.msg_history {
  height: 516px;
  overflow-y: auto;
}
</style>