<template>
  <div class="type_msg">
    <div class="input_msg_write">
      <input type="text" class="write_msg" placeholder="Type a message" v-model="msg" @keyup.enter="sendMsg"/>
      <button class="msg_send_btn text-center" type="button" @click="sendMsg">
        <!-- <i class="fa fa-paper-plane-o" aria-hidden="true"></i> -->
        <font-awesome-icon id="icon" icon="paper-plane"/>
      </button>
    </div>
  </div>
</template>

<script>
import fb from "../firebase";

export default {

  data() {
    return {
      msg: "",
    };
  },
  methods: {
    sendMsg(e) {
      e.preventDefault();
      let text = this.msg;
      this.msg = "";

      if(text === "") return;

      var db = fb.firestore();
      db.collection("messages").add({
        sender: this.$store.state.user.user,
        sentAt: fb.firestore.FieldValue.serverTimestamp(),
        text: text,
      });
    
    },
  },
};
</script>

<style scoped>


.input_msg_write input {
  color: #000000;
  font-size: 15px;
  margin-top: 5px;
  width: 90%;
  outline-color: unset;
  border: 2px solid black;
  border-radius: 3px;
  padding: 10px;
  display: block;
  opacity: 0.6;
  position: relative;
}

.type_msg {
  border-top: 1px solid #c4c4c4;
  margin: 5px;
}
.msg_send_btn {
  background: #8B0000  none repeat scroll 0 0;
  border: medium none;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  font-size: 17px;
  height: 33px;
  position: absolute;
  right: 17px;
  top: 11px;
  width: 33px;
  padding: 10px;
  outline: 0;

}

#icon{
  position: absolute;
  top: 25%;
  left: 25%;
}
.msg_send_btn:hover{
  transform: scale(1.2);
}
</style>