<template>
  <div id="chat">
    <div class="header">
    </div>

    <div @v-chat-scroll-top-reached="scrollShow" id="chatWrapper" class="wrapper" v-chat-scroll="{enabled:true, always:false, smooth: true}">
      <MessageWrapper v-for="msg in messages" :key="msg.id" :msg="msg"/>
    </div>
    <div class="message-form">
      <MessageForm/>
    </div>


  </div>
</template>

<script>

import fb from "../firebase";
import MessageForm from "./MessageForm";
import MessageWrapper from "./MessageWrapper";

export default {
  created() {

    //load n messages at start.
    var db = fb.firestore();
    db.collection("messages")
      .orderBy("sentAt", "desc")
      .limit(this.messagesLoaded)
      .get()
      .then(data => {
        this.messages = data.docs.map(doc => doc.data());
        this.messages = this.messages.reverse();
      })
    
    //let me listen for any updates


    db.collection("messages")
      .orderBy("sentAt", "desc")
      .onSnapshot(() =>{
        this.loadData();
      })
  },

  data() {
    return {
      text: "",
      messages: [],
      username: "tester",
      messagesLoaded: 20,
      stopScroll: false,
      publicPath: process.env.BASE_URL,

    };
  },
  components: {
    MessageForm,
    MessageWrapper,
  },

  methods: {
    loadData(){
      var db = fb.firestore();
      db.collection("messages")
        .orderBy("sentAt", "desc")
        .limit(this.messagesLoaded)
        .get()
        .then(data => {
          this.messages = data.docs.map(doc => doc.data());
          this.messages = this.messages.reverse();
        })
      
      db.collection("messages")
        .get()
        .then(data =>{
           if(data.docs.length === this.messages.length){
            this.stopScroll = true;
          }
        
        })
    },
    scrollShow(){

      this.messagesLoaded += 10;
      if(!this.stopScroll){
        this.loadData();
        var chatWrapper = document.getElementById("chatWrapper");
        chatWrapper.scrollTop = chatWrapper.scrollHeight/5;
      }
    }
  },
};
</script>

<style scoped>

#chat {

  height: 550px;
  width: 700px;
  /* margin: 0 auto;
  margin-top: 40px; */
  opacity: 0.95;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px 3px;

  background-image: url("../assets/chat-bg.png");
  /* background-image: url("../assets/chat-bg-1.jpg"); */
  background-size: cover;
}

.wrapper {
  height: 90%;
  overflow-y: scroll;
}

.message-form {
  position: relative;
  bottom: 0;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

</style>
